canvas {
   width: 100vw;
   height: 100vh;
}

.bg-image {
  display: none;
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
}

.Preloader {
   width: 100vw;
   height: 100vh;
   display: grid;
   place-items: center;
   background: #000000;
}

.Preloader img {
    width: 200px;
    height: 200px;
    object-fit: contain;
}